.paradox text{
    fill:#ffffffc5;
    font-weight: 1000;
    letter-spacing: 0.5px;
    stroke:#004353;
    stroke-width:0.3;
    stroke-dasharray: 0% 100%;
    transition:linear 0.8s;
    transform: scaleY(1.3);
}

#outer-logo-div:hover #paradox-text{
    stroke-width:0.1;
    fill:rgb(122, 224, 255);
    stroke: #005e7a;
    stroke-dashoffset: 50%;
    stroke-linecap:round;
    stroke-dasharray: 70% 0%;
    cursor:pointer;
}
.Paradox__logo{
    position: absolute;
    top: 8vw;
    padding: 3vw;
    transition: .8s ease-in-out;
    transform: rotate(0deg);
    -webkit-filter: invert(10%) sepia(100%) saturate(0%) hue-rotate(183deg) brightness(100%) contrast(80%);
