
.logo{
    display: flex;
    align-items: center;
    flex-direction: row;
    margin: 10% 0;
}
#symbol{
    height: 140px;
    width: auto;
}
@font-face {
    font-family: 'robodronregular';
    src: url('../robodron-webfont.woff2') format('woff2'),
         url('../robodron-webfont.woff') format('woff');
}
.paradox{
  font-family: 'robodronregular';
}
.paradox text{
    fill:#ffffffc5;
    font-weight: 1000;
    letter-spacing: 0.5px;
    stroke:#004353;
    stroke-width:0.3;
    stroke-dasharray: 0% 100%;
    transition:linear 0.8s;
    transform: scaleY(1.3);
}

#outer-logo-div:hover #paradox-text{
    stroke-width:0.1;
    fill:rgb(122, 224, 255);
    stroke: #005e7a;
    stroke-dashoffset: 50%;
    stroke-linecap:round;
    stroke-dasharray: 70% 0%;
    cursor:pointer;
}
.Paradox__logo{
    position: absolute;
    top: 8vw;
    padding: 3vw;
    transition: .8s ease-in-out;
    transform: rotate(0deg);
    filter: invert(10%) sepia(100%) saturate(0%) hue-rotate(183deg) brightness(100%) contrast(80%);
}
#outer-logo-div:hover .Paradox__logo{
    cursor:pointer;
    transform: rotate(-90deg);
    filter: none;
}
#cryptic_hunt{
    fill:rgba(0, 49, 65, 0);
    font-weight: 600;
    letter-spacing: 0.5px;
    transition:linear 0.8s;
    font-size:5px;
    transform: translate(-20%,20%);
    transition: .8s linear;
}

#outer-logo-div:hover #cryptic_hunt{
    font-size:5px;
    transform: translate(-20%,20%);
    fill:rgba(255, 255, 255, 0.973);
    text-shadow:1px 10px 15px rgba(0, 0, 0, 0.822);
}

.homepage__main{
    height: 100vh;
}

.button-outer-box{
    margin:1% auto;
    display:inline-block;
}

.homepage__about-section{
    width:70%;
    margin:2% auto 0;
    background-color:#04c2fc11;
    background:transparent;
    display:block;
}


h2.about-heading {
  overflow: hidden;
  text-align: center;
  text-shadow: 0px 0px 50px 20px rgb(3, 121, 121),0px 0px 50px 30px #0ff;
}

h2.about-heading:before,
h2.about-heading:after {
    background-color: rgba(118, 223, 255, 0.877);
    content: "";
    display: inline-block;
    height: 5px;
    position: relative;
    vertical-align: middle;
    width: 50%;
    text-shadow: 0px 0px 50px 20px rgb(3, 121, 121),0px 0px 50px 30px #0ff;
}

h2.about-heading:before {
  right: 0.5em;
  margin-left: -50%;
}

h2.about-heading:after {
  left: 0.5em;
  margin-right: -50%;
}

.section-description{
    font-size:2.8vh;
    opacity:0.85;
    margin:3%;
    padding:2%;
    white-space: pre-line;
    word-break: break-word;
}

.about-heading{
    font-size:45px;
    margin:0 auto 5% auto;
    margin-bottom:3%;
    background-color: transparent;

}
.homebtn{
    border: 0 solid;
    padding:5% 20%;
    outline: 1px solid;
    outline-color: rgba(213, 247, 255, 0.644);
    outline-offset: 0px;
    transition: all 1250ms cubic-bezier(0.19, 1, 0.22, 1);
    color:rgba(213, 247, 255, 0.712);
    background:rgba(34, 34, 34, 0.068);
    font-size:1.3rem;
    width:14vw;
    text-align: center;
    font-family:"rajdhani", sans-serif;
    width: 100%;
    text-align: center;
}

.homebtn:hover{
    border: 1px solid;
    padding:5% 20%;
    box-shadow: 0 0 10px rgba(116, 227, 255, 0.966), 0 0 20px rgba(43, 3, 95, 0.24);
    outline-color: rgba(255, 255, 255, 0);
    outline-offset: 15px;
    color : rgba(213, 247, 255, 0.966);
}


#discord{
    background:rgba(115, 8, 255, 0.39);
    background: rgb(115, 121, 207);
    outline:none;
    color:rgba(10, 10, 10, 0.829);
    border-radius:5px;
    font-weight: 600;
    transition: all 1250ms cubic-bezier(0.19, 1, 0.22, 1);
    padding:4% 1%;
}

#DiscordLogo{
    width:30px;
    height:30px;
    margin:0px 5px;
    color:black;
}

#discord:hover{
    outline:none;
    color : rgba(233, 213, 255, 0.966);
}


#discord:hover .colorchange{
    filter: invert(85%) sepia(100%) saturate(7481%) hue-rotate(183deg) brightness(100%) contrast(100%);
}

/* Scroll animation */
#sd-container {
    position:absolute;
    left:48%;
    bottom:4vw;
    margin:auto;
}

.arrow {
  position: relative;
  width: 42px;
  height: 16px;
  opacity: 0;
  margin:auto;
}
.arrow::before, .arrow::after {
  content: "";
  width: 21px;
  height: 2px;
  background-color: #fff;
  border-radius: 2px;
  display: inline-block;
}
.arrow::before {
  transform: rotate(45deg) translateX(25%);
}
.arrow::after {
  transform: rotate(-45deg) translateX(-25%);
}
.arrow:nth-child(1) {
  top: -50px;
  opacity: 1;
  -webkit-animation: arrow2 1s ease 0s infinite;
          animation: arrow2 1s ease 0s infinite;
}
.arrow:nth-child(2) {
  top: -25px;
  -webkit-animation: arrow1 1s ease 0.25s infinite;
          animation: arrow1 1s ease 0.25s infinite;
}

@-webkit-keyframes arrow1 {
  from {
    opacity: 0;
    top: -25px;
  }
  to {
    opacity: 0;
    top: 0px;
  }
  50% {
    opacity: 1;
  }
}

@keyframes arrow1 {
  from {
    opacity: 0;
    top: -25px;
  }
  to {
    opacity: 0;
    top: 0px;
  }
  50% {
    opacity: 1;
  }
}
@-webkit-keyframes arrow2 {
  from {
    opacity: 0;
    top: -35px;
  }
  to {
    opacity: 0;
    top: -10px;
  }
  50% {
    opacity: 1;
  }
}
@keyframes arrow2 {
  from {
    opacity: 0;
    top: -35px;
  }
  to {
    opacity: 0;
    top: -10px;
  }
  50% {
    opacity: 1;
  }
}

.highlight{
    color: rgb(118, 223, 255);
}

.homepage__sponsors-section , .homepage__rules-section{
    margin-top:2%;
    background:transparent;
}

.sponsors-section,.Rules-section{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width:100%;
}

.sponsors-section a{
    margin:2%;
    align-items: center;
    display: flex;
    justify-content: center;
}
#ieeelink{
    margin:2% 20%;
}
#ieeelogo{
    width:30rem;
}
#ieeelogo h3{
    font-weight: 700;
    font-size:40px;
}

.bullet{
    height:15px;
    width:15px;
}

.sponsor-tiles
{
    width:16rem;
    padding:1%;
    justify-content: center;
