}
.contact-info h2, .social-panel h2{
    color:rgba(255, 255, 255, 0.8);
}
.contact-info ul{
    list-style: none;
    padding:0;
}
.copyright{
    text-align:center;
    font-size:22px;
}
.social-logos{
    height:35px;
    width:35px;
    margin: 80px 10px;
}
.socialmedialinks{
    margin: 0 70px;
}
.homepage__top{
    min-height: 90vh;
}
#footer_logo{
    width: 400px;
}

@media (min-width: 767px) and (max-height: 700px) {
    .logo-normal{
        display: none!important;
    }
    .logo-height{
        display: block!important;
    }
    .Paradox__logo{
        top: 5.6vw;
    }
}

@media (max-width: 1260px) {
    .socialmedialinks{
        margin: 0 30px;
    }
}

@media (max-width: 1100px) {
    .Paradox__logo{
        top: 7vw;
        padding: 3.2vw;
    }
    .socialmedialinks{
        margin: 0 10px;
    }
}
/*##### mobile view ###### */
@media only screen and (max-width: 767px) {
    #DiscordLogo__text{
        display: none;
    }
    .social-logos{
        margin: 20px 10px;
    }
    #footer_logo{
        width: 95vw;
    }
    .Paradox__logo{
        display:none;
    }
    .paradox{
        height: 45vh;
        width: 90vw;
    }
    .paradox text{
        font-size:2vw;
        transform: scaleY(1.4) translateY(-10%);
    }
    #outer-logo-div:hover #paradox-text{
        stroke-width:0.1;
    }
    #cryptic_hunt{
        font-weight: 400;
        font-size:3.2px;
        transform:translate(10%,8%);
        transition: .8s linear;
    }
    #outer-logo-div:hover #cryptic_hunt{
        font-weight: 400;
        font-size:3.2px;
        transform:translate(10%,8%);
    }
    #sd-container{
        display:none;
    }
}



